<template>
    <div id="PerspectiveCapture">
        <div class="per-bg" ref="canvasBox"></div>
    </div>
</template>

<script>
import * as THREE from 'three';

export default {
    data() {
        return {
            camera:null,//相机
            scene:null,//场景
            render:null,//渲染实例
            boxLingthGroud:null,//小球 组对象
            numberBoxList:[], //小球数量 记录随机数组
        }
    },
    mounted() {
        this.init()
        this.animate()
    },
    beforeDestroy() {
      cancelAnimationFrame(this.Animation);
    },
    methods: {
        init(){
            let that = this
            //初始化相机
            this.camera = new THREE.PerspectiveCamera(45,window.innerWidth / window.innerHeight, 1, 30000)
            this.camera.position.y = 10
            //初始化场景
            this.scene = new THREE.Scene();
            // this.scene.background = new THREE.Color('#ffffff')
            //渲染实例,并执行抗锯齿
            this.render = new THREE.WebGLRenderer({antialias:true})
            this.render.setPixelRatio( window.devicePixelRatio ) //设置设备像素比
            //设置渲染窗口大小
            this.render.setSize( this.$refs.canvasBox.clientWidth + 370, window.innerHeight );
            
            //将渲染器挂载到页面上
            this.$refs.canvasBox.appendChild( this.render.domElement )
            //监听窗口变化
            window.addEventListener('resize', this.onWindowResize, false)
            

            //创建实物
            this.addDirLight()
            this.addFloor()
            this.addPointerLine()
        },
        //渲染窗口自适应
        onWindowResize() {
            this.camera.aspect = window.innerWidth / window.innerHeight;
            this.camera.updateProjectionMatrix();
            this.render.setSize( window.innerWidth, window.innerHeight );
        },
        //创建平行光
        addDirLight(){
            let dirLight = new THREE.DirectionalLight('#ffffff',1,100)
            dirLight.position.set(0,1,0)
            dirLight.castShadow = true //开启阴影   

            this.scene.add(dirLight)

            // 设置计算阴影的区域，最好刚好紧密包围在对象周围
            // 计算阴影的区域过大：模糊  过小：看不到或显示不完整
            dirLight.shadow.camera.near = 0.5;
            dirLight.shadow.camera.far = 300;
            dirLight.shadow.camera.left = -50;
            dirLight.shadow.camera.right = 50;
            dirLight.shadow.camera.top = 200;
            dirLight.shadow.camera.bottom = -10;
            // 设置mapSize属性可以使阴影更清晰，不那么模糊
            dirLight.shadow.mapSize.set(1024,1024)
        },
        //创建地板
        addFloor(){
            let floorGeometry = new THREE.PlaneGeometry(2000, 2000, 1, 1)
            floorGeometry.rotateX( - Math.PI /2 ) //旋转地面摆向
            
            let floorMaterial = new THREE.MeshPhongMaterial({
                // color:"rgba(255,200,100)"
                color:'#200000',
                // emissiveIntensity:0,
                shininess:50
            })

            let floor = new THREE.Mesh( floorGeometry, floorMaterial )

            this.scene.add( floor )
        },
        // 创建小球
        addPointerLine(){
            let sphere,pointerLine;
            this.boxLingthGroud = new THREE.Group()
            for(let i = 0; i < 50; i++){
                let r = this.random(0, 256), g = this.random(0, 256), b = this.random(0, 256);
                sphere = new THREE.Points(new THREE.SphereGeometry(5,10,10), new THREE.PointsMaterial({color: "rgb("+ r +","+ g +","+ b +")"}))
                pointerLine = new THREE.PointLight("rgb("+ r +","+ g +","+ b +")", 2, 1000)
                pointerLine.add(sphere)
                // pointerLine.position.y = 10
                this.boxLingthGroud.add( pointerLine )
                this.numberBoxList.push(Number((Math.random() * (0 + 1)).toFixed(2)))
            }
            this.scene.add( this.boxLingthGroud )
        },
        //实时渲染
        animate(){
            let that = this,timeLingth = Date.now() * 0.00025,d=100;
            this.Animation = requestAnimationFrame( that.animate );

            //每个小球运动位置
            for(let i = 0; i < this.boxLingthGroud.children.length; i++){
                this.boxLingthGroud.children[i].position.x = Math.cos( timeLingth * (this.numberBoxList[i]+0.1) ) *d;
                this.boxLingthGroud.children[i].position.z = Math.cos( timeLingth * (this.numberBoxList[i]+0.3) ) *d;
                this.boxLingthGroud.children[i].position.y = Math.sin( timeLingth * (this.numberBoxList[i]+0.2) ) *d < 8 ? 8 : Math.sin( timeLingth * (this.numberBoxList[i]+0.2) )*d;
                // this.boxLingthGroud.children[i].position.y = Math.sin( timeLingth * (this.numberBoxList[i]+0.2) ) *d;
            }
            // 摄像机位置与朝向
            this.camera.position.set( this.boxLingthGroud.children[0].position.x, this.boxLingthGroud.children[0].position.y, this.boxLingthGroud.children[0].position.z )
            // this.camera.lookAt( this.boxLingthGroud.children[1].position.x, this.boxLingthGroud.children[1].position.y, this.boxLingthGroud.children[1].position.z )
            this.camera.lookAt( 0,0,0 )

            this.render.render( this.scene, this.camera )
        },
        // 范围随机数
        random(lower, upper) {
            return Math.floor(Math.random() * (upper - lower)) + lower;
        },
    },
}
</script>

<style lang="less" scoped>
    #PerspectiveCapture{
        margin-left: -200px;
        // .per-bg{
        //     position: absolute;
        //     width: 100vw;
        //     height: 100%;
        //     background-color: rgba(0,0,0,0.5);
        // }
    }
</style>


